import allPage from "@/mixin/allPage_MX";
export default {
	mixins: [allPage],
	components: {
		
	},
	beforeCreate() {

	},
	data() {
		return {
			options:null,
			currentIndex: null,
			taskDetail:{},
			journalList: [{
				'name': '上刊（2）',
				'time': '05.01-05.01'
			},{
				'name': '监播1',
				'time': '05.01-05.02'
			},{
				'name': '监播2',
				'time': '05.01-05.03'
			},{
				'name': '下刊',
				'time': '05.01-05.04'
			}],
			showList:['中国', '美国', '巴西', '日本'],
		}
	},
	computed: {
		userLogin() {
			return this.$store.state.userLogin.userLogin
		},
	},
	onShow: function() {
		
	},
	onLoad: function(options) {
		this.options = options;
		this.getData();
	},
	onReady: function() {
		
	},
	onHide: function(options) {
		
	},
	onReachBottom: function(options) {
		
	},

	onPullDownRefresh() {
		
	},
	mounted() {
		
	},
	watch: {
		
	},
	onPageScroll: function(e) {
		
	},
	methods: {
		getData(){
			new Promise(resolve => {
				let parms = ',"openid":"' + this.userLogin.user.openid + '"},"spotFileQuery":{"pageNo":"1","pageSize":"12","keyword":"","taskId":"' + this.options.id + '"}';
				this.$store.dispatch('myList/getSpotFilesList', {parms,
					callback: (res1) => {
						console.log(res1);
						if (res1.errorCode == 0) {
							let data = res1.spotFileResult.spotFiles;
							resolve(data)
						}else{
							uni.showToast({
								title: res1.errorMsg,
								icon: 'none',
								mask: true
							})
						}
					},
				})
			}).then(res1 => {
				let parms = '},"taskQuery":{"taskId":' + this.options.id + '}';
				this.$store.dispatch('details/getTaskDetail', {parms,
					callback: (res2) => {
						console.log(res2);
						if (res2.errorCode == 0) {
							let data = res2.taskResult.tasks[0];
							for(let i=0; i<data.monitorStages.length; i++){
								if(data.monitorStages[i].IsEnabled){
									this.currentIndex = i;
								}
								data.monitorStages[i].spotFiles = []
								for(let s=0; s<data.monitorStages[i].value2; s++){
									let index = s+1;
									data.monitorStages[i].spotFiles.push({
										"spotClassType": index,
										"spotClassTypeName": "图片" + index,
									})
								}
								data.monitorStages[i].spotFiles.push({
									"spotClassType": Number(data.monitorStages[i].value2) + 1,
									"spotClassTypeName": "视频",
								})
								for(let z=0; z<data.monitorStages[i].spotFiles.length; z++){
									for(let j=0; j<res1.length; j++){
										if(data.monitorStages[i].displayName == res1[j].monitorStageName && data.monitorStages[i].spotFiles[z].spotClassTypeName == res1[j].spotClassTypeName){
											data.monitorStages[i].spotFiles[z] = res1[j]
										}
									}
								}
							}
							console.log(data)
							this.taskDetail = data;
						}else{
							uni.showToast({
								title: res2.errorMsg,
								icon: 'none',
								mask: true
							})
						}
					},
				})
			})
			
		},
		tabChange(index){
			this.currentIndex = index;
		},
		goPhotoSubmission(cameraType){
			uni.navigateTo({
			    url: "/pages/photoSubmission/photoSubmission?cameraType=" + cameraType + "&implement=1"
			})
		}
	},
}
